<template>
    <div class="content">
        <div class="intro-y col-span-12 lg:col-span-12">
            <div class="intro-y box">
                <div
                    class="flex flex-col sm:flex-row items-center p-5 border-b border-slate-200/60 dark:border-darkmode-400">
                    <h2 class="font-medium text-base mr-auto">
                        Add User Form
                    </h2>
                </div>
                <div id="vertical-form" class="p-5">
                    <div class="preview">
                        <form v-on:submit.prevent="onSubmit" method="post">
                            <div class="mt-3">
                                <label for="vertical-form-2" class="form-label">First Name</label>
                                <input id="vertical-form-2" type="text" class="form-control" placeholder="First Name"
                                    v-model="firstName" name="firstName">
                                <p v-show="!firstName" class="text-danger">First name is required.</p>
                            </div>
                            <div class="mt-3">
                                <label for="vertical-form-2" class="form-label">Last Name</label>
                                <input id="vertical-form-2" type="text" class="form-control" placeholder="Last Name"
                                    v-model="lastName" name="lastName">
                                <p v-show="!lastName" class="text-danger">Last name is required.</p>
                            </div>
                            <div class="mt-3">
                                <label for="vertical-form-2" class="form-label">Email</label>
                                <input id="vertical-form-2" type="text" class="form-control" placeholder="email@gmail.com"
                                    v-model="email">
                                <span v-if="msg.email" class="text-danger">{{ msg.email }}</span>
                                <span v-if="emailError" class="text-danger">{{ emailError }}</span>
                                <p v-show="!email" class="text-danger">Email is required.</p>

                            </div>
                            <div class="mt-3">
                                <label for="vertical-form-2" class="form-label">Mobile Number</label>
                                <input id="vertical-form-2" type="text" class="form-control" placeholder="Mobile Number"
                                    v-model="phoneNumber" name="phoneNumber"
                                    :class="{ valid: isValidMobileNumber, invalid: !isValidMobileNumber }"
                                    @keyup="validateMobileNumber">
                                <div class="text-danger" v-if="!isValidMobileNumber">
                                    Invalid Mobile Number
                                </div>
                                <p v-show="!phoneNumber" class="text-danger">Phone Number is required.</p>
                            </div>
                            <div class="mt-3">
                                <label for="vertical-form-2" class="form-label">Password</label>
                                <input id="vertical-form-2" type="password" class="form-control" v-model="password"
                                    name="password" @input="validatePassword">
                                <span v-if="passwordError" class="text-danger">{{ passwordError }}</span>
                                <p v-show="!password" class="text-danger">Password is required.</p>
                            </div>
                            <div class="mt-3">
                                <label for="vertical-form-2" class="form-label">Date of Year</label>
                                <input id="vertical-form-2" type="text" class="form-control" v-model="year" name="year" />
                                <p v-show="!year" class="text-danger">Date of Year is required.</p>
                            </div>
                            <div class="mt-3">
                                <label for="vertical-form-2" class="form-label">Months of Birth</label>
                                <select v-model="months"
                                    class="block appearance-none w-full bg-white border border-gray-400 hover:border-gray-500 px-4 py-2 pr-8 rounded shadow leading-tight focus:outline-none focus:shadow-outline">
                                    <option value="">Select a month</option>
                                    <option v-for="(month, index) in monthName" :key="index" :value="month">{{ month }}
                                    </option>
                                </select>
                                <!-- <input id="vertical-form-2" type="date" class="form-control" v-model="dob"
                                    name="dob" /> -->
                                <p v-show="!months" class="text-danger">Months of is required.</p>
                            </div>

                            <div class="mt-3">
                                <label for="vertical-form-2" class="form-label">Country</label>
                                <input id="vertical-form-2" type="text" class="form-control" v-model="country"
                                    name="country" />
                                <p v-show="!country" class="text-danger">Country is required.</p>
                            </div>
                            <button class="btn btn-primary mt-5" type="submit" :disabled="formIsEmpty">Add</button>
                            <div v-if="submitError">{{ submitError }}</div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import {
    postRequest
} from '../../pages/ApiFunctions';

export default {
    name: 'AddUser',
    data() {
        return {
            monthName: ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'],
            emailError: '',
            msg: [],
            firstName: "",
            lastName: "",
            email: "",
            phoneNumber: "",
            password: "",
            year: "",
            months: '',
            country: "",
            disabled: [true, true],
            isValidMobileNumber: true,
            submitError: null,
            passwordError: '',
            errors: {
                firstName: '',
                lastName: '',
                email: '',
                phoneNumber: '',
                password: '',
                year: '',
                months: '',
                county: ''
            },

        }
    },
    methods: {
        async onSubmit() {
            this.validate();
            const userData = {
                firstName: this.firstName,
                lastName: this.lastName,
                email: this.email,
                password: this.password,
                phoneNumber: this.phoneNumber,
                year: this.year,
                months: this.months,
                country: this.country
            }
            try {
                const response = await postRequest('/register', userData)

                if (response.error) {
                    this.emailError = response.error;
                } else {
                    this.emailError = '';
                    this.$router.push({
                        path: '/admin/user-data-list',
                        name: 'UserDataList'
                    })
                }
            } catch (err) {
                this.submitError = 'Error submitting form. Please try again later.';
            }

        },
        validateEmail(value) {
            if (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(value)) {
                this.msg['email'] = '';
            } else {
                this.msg['email'] = 'Invalid Email Address';
            }
        },
        validateMobileNumber() {
            const validationRegex = /^\d{10}$/;
            if (this.phoneNumber.match(validationRegex)) {
                this.isValidMobileNumber = true;
            } else {
                this.isValidMobileNumber = false;
            }
        },
        validate() {
            if (!this.firstName) {
                this.errors.firstName = 'First name is required.';
            } else {
                this.errors.firstName = '';
            }
            if (!this.lastName) {
                this.errors.lastName = 'Last name is required.';
            } else {
                this.errors.lastName = '';
            }
            if (!this.email) {
                this.errors.email = 'Email is required.';
            } else {
                this.errors.email = '';
            }
            if (!this.phoneNumber) {
                this.errors.phoneNumber = 'Phone Number is required.';
            } else {
                this.errors.phoneNumber = '';
            }
            if (!this.password) {
                this.errors.password = 'Password is required.';
            } else {
                this.errors.password = '';
            }
            if (!this.year) {
                this.errors.year = 'year is required.';
            } else {
                this.errors.year = '';
            }
            if (!this.months) {
                this.errors.months = 'months is required.';
            } else {
                this.errors.months = '';
            }
            if (!this.country) {
                this.errors.country = 'country is required.';
            } else {
                this.errors.country = '';
            }
        },
        validatePassword() {
            const passwordRegex = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[a-zA-Z]).{8,}$/;
            if (!passwordRegex.test(this.password)) {
                this.passwordError = 'Password must be at least 8 characters long and contain at least one uppercase letter, one lowercase letter, and one number';
                return false;
            } else {
                this.passwordError = '';
                return true;
            }
        }

    },
    watch: {
        email(value) {
            // binding this to the data value in the email input
            this.email = value;
            this.validateEmail(value);
        }
    },
    computed: {
        formIsEmpty() {
            return !this.firstName || !this.lastName || !this.email || !this.phoneNumber || !this.password || !this.year || !this.months || !this.country
        }
    }

}
</script>



